<template>
  <div id="task-list" class="task-list">
    <add-task ref="addTask" @on-success="addSuccess"></add-task>
    <div class="task-table-box">
      <h2>欢迎，记录您的任务便于享受更好的功能</h2>
      <div class="tool-box">
        <Button type="ghost" @click="addTask">添加</Button>
        <Button type="ghost" @click="delTask">删除</Button>
      </div>
      <div class="task-card">
        <div class="task-item-box" v-for="(item, $index) in taskList" @click="editTask(item)">
          <span class="task-item">{{item.type}}</span>
          <span class="task-item">{{item.name}}</span>
          <span class="task-item task-item-time" v-if="!item.startTime" @click.stop="updateTime(item)">
            <Icon type="play"></Icon>
          </span>
          <span class="task-item task-item-time" v-if="item.startTime&&!item.endTime" @click.stop="updateTime(item)">
            <Icon type="stop"></Icon>
          </span>
        </div>
      </div>
      <!--<span class="task-td">-->
        <!--<span v-if="item.startTime" :title="format(item.startTime)">{{format(item.startTime)}}</span>-->
        <!--<span v-if="!item.startTime"><button @click="updateTime('startTime', item)">开始</button></span>-->
      <!--</span>-->
      <!--<span class="task-td">-->
        <!--<span v-if="item.endTime" :title="format(item.endTime)">{{format(item.endTime)}}</span>-->
        <!--<span v-if="!item.endTime"><button @click="updateTime('endTime', item)">结束</button></span>-->
      <!--</span>-->
      <!--<Table :columns="taskListTitle" :data="taskList"></Table>-->
    </div>
  </div>
</template>
<script>
  import Ajax from '../js/ajax';
  import CONFIG from '../js/config';
  import addTask from './addTask';
  import vTime from './vTime';
  export default {
    data() {
      return {
        showModal: false,
        user: '',
        taskType: [],
        taskTypePlan: true,
        taskTypeDoing: false,
        taskTypeDone: false,
        taskListTitle: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: '分类',
            key: 'type'
          },
          {
            title: '任务描述',
            key: 'name'
          },
          {
            title: '优先级',
            key: 'level'
          },
          {
            title: '备注',
            key: 'memo'
          },
          {
            title: '计划完成',
            key: 'planTime'
          },
          {
            title: '开始时间',
            key: 'startTime'
          },
          {
            title: '结束时间',
            key: 'endTime'
          }
        ],
        taskList: [],
        task: {},
        isAddTask: false,
        isShowTips: false,
        active: -1,
        isEdit: false,
        pageNumber: 1,
        curPageNumber: 0,
        offset: 0
      }
    },
    methods: {
      addTask () {
        this.$refs.addTask.showModal();
      },

      addSuccess: function () {
        console.log('success');
        this.getTaskList();
      },

      editTask (item) {
        this.$refs.addTask.showModal(item);

        return;
        if (this.isEdit) {
          this.isEdit = false;
          // 更新数据
          this.updateTask(item);
        }
      },

      updateTime(item) {
        let time = new Date().getTime();
        let task = {
            _id: item._id,
        };
        if (!item.startTime) {
          // 任务未开始
          task.startTime = time;
        } else {
          // 任务已经开始
          task.endTime = time;
        }
        Ajax.update(task, result => {
          if (result.success) {
            this.getTaskList();
          }
        });
      },

      // 更新表格信息
      getTaskList() {
        let filter = {};
        Ajax.fetch(filter, result => {
          // 前端过滤后显示
          if (result.success) {
            this.taskList = result.data;
          }
        });
      },

      delTask(item) {
        if (this.active === -1) {
          this.$Notice.warning({
            title: '请选择你要操作的数据',
            duration: 2
          });
          return;
        }
        var item = this.taskList[this.active];
        Ajax.delete(item._id, result => {
          if (result.success) {
            // 手动移除DOM，不请求task列表
            for (let i = 0; i < this.taskList.length; i++) {
              if (this.taskList[i]._id === item._id) {
                this.taskList.splice(i, 1);
                this.active = -1; // 删除成功取消选中的行
                return;
              }
            }
          }
        });
      }
    },
    created() {
      this.getTaskList();
    },
    components: {
      addTask,
      vTime
    },
    watch: {

    }
  }
</script>
<style lang="scss" scope>
  .task-card {
    width: 100%;
    height: 200px;
    overflow: auto;
    font-size: 0;

    .task-item-box {
      margin: 10px;
      display: inline-block;

      &:hover .task-item {
        background: #ebf7ff;
      }

      .task-item {
        font-size: 12px;
        border: 1px solid #ddd;
        border-left: none;
        padding: 1px 3px;
        display: inline-block;
        height: 26px;
        line-height: 21px;
        min-width: 26px;
        text-align: center;
        vertical-align: top;
        cursor: pointer;

        &:first-child {
          border-left: 1px solid #ddd;
        }

        &:hover {

        }
      }

      .task-item-time:hover {
        color: #e4353c;
      }
    }
  }
</style>



