<template>
  <Modal
    v-model="show"
    title="添加任务">
    <Form ref="taskItem" :model="taskItem" :rules="ruleValidate" :label-width="80">
      <Form-item label="优先级" prop="level">
        <Select v-model="taskItem.level">
          <Option value="1">紧急重要</Option>
          <Option value="2">紧急不重要</Option>
          <Option value="3">不紧急重要</Option>
          <Option value="4">不紧急不重要</Option>
        </Select>
      </Form-item>
      <Form-item label="分类" prop="type">
        <Input v-model="taskItem.type" placeholder="请输入分类"></Input>
      </Form-item>
      <Form-item label="描述" prop="name">
        <Input v-model="taskItem.name" placeholder="请输入描述"></Input>
      </Form-item>
      <Form-item label="计划完成">
        <Date-picker type="date" :editable="false" placeholder="选择日期" v-model="taskItem.planTime"></Date-picker>
      </Form-item>
      <Form-item label="开始时间" v-if="!isNew">
        <Date-picker type="date" :editable="false" placeholder="选择日期" v-model="taskItem.startTime"></Date-picker>
      </Form-item>
      <Form-item label="结束时间" v-if="!isNew">
        <Date-picker type="date" :editable="false" placeholder="选择日期" v-model="taskItem.endTime"></Date-picker>
      </Form-item>
      <Form-item label="备注">
        <Input v-model="taskItem.memo" type="textarea" :autosize="{minRows:3}"></Input>
      </Form-item>
    </Form>
    <div slot="footer">
      <Button type="primary" @click="ok">确定</Button>
      <Button type="ghost" @click="cancel">取消</Button>
    </div>
  </Modal>
</template>
<script>
  import Ajax from '../js/ajax';
  export default {
    data() {
      return {
        show: false,
        isNew: true,
        taskItem: {
          level: '1',
          type: '',
          name: '',
          planTime: '',
          startTime: '',
          endTime: '',
          memo: ''
        },
        ruleValidate: {
          type: [
            {required: true, message: '类型不能为空', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '描述不能为空', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      showModal (item) {
          this.show = true;

          // 编辑
          if (item) {
            this.isNew = false;
            this.taskItem = JSON.parse(JSON.stringify(item));
            if (this.taskItem.planTime) {
              this.taskItem.planTime = new Date(Number(this.taskItem.planTime));
            }
            if (this.taskItem.startTime) {
              this.taskItem.startTime = new Date(Number(this.taskItem.startTime));
            }
            if (this.taskItem.endTime) {
              this.taskItem.endTime = new Date(Number(this.taskItem.endTime));
            }
          } else {
              this.isNew = true;
          }
      },
      validate (name) {
        return new Promise(resolve => {
          this.$refs[name].validate((valid) => {
            if (valid) {
              resolve(true);
            } else {
              resolve(false);
            }
          });
        });
      },
      ok () {
        this.validate('taskItem').then(result => {
          if (result) {
            this.$emit('input', false);
            if (this.taskItem.planTime) {
              this.taskItem.planTime = new Date(this.taskItem.planTime).getTime();
            }
            if (this.taskItem.startTime) {
              this.taskItem.startTime = new Date(this.taskItem.startTime).getTime();
            }
            if (this.taskItem.endTime) {
              this.taskItem.endTime = new Date(this.taskItem.endTime).getTime();
            }
            if (this.taskItem._id) {
              // 更新
              Ajax.update(this.taskItem, result => {
                if (result.success) {
                  this.$emit('on-success');
                  this.cancel();
                }
              });
            } else {
              // 新增
              Ajax.save(this.taskItem, result => {
                if (result.success) {
                  this.$emit('on-success');
                  this.cancel();
                }
              });
            }
          }
        });
        return false;
        let taskInfo = {
          type: this.task.type || '',
          name: this.task.name || '',
          level: this.task.level || '',
          planTime: this.task.planTime || '',
          memo: this.task.memo || '',
          startTime: this.task.startTime || '',
          endTime: this.task.endTime || ''
        };

      },
      cancel () {
        this.show = false;
        this.$emit('input', false);
        window.setTimeout(() => {
          this.$refs['taskItem'].resetFields();
          this.taskItem = {
            level: '1',
            type: '',
            name: '',
            planTime: '',
            memo: ''
          };
        }, 250);
      },
      change (val) {

      }
    },
    watch: {

    },
    props: {
      data: {
        type: Array
      }
    },
    created () {
//        this.showModal = this.show;
      console.log(this.data);
    }
  }
</script>
<style scope>

</style>



